.answer {
	opacity: 0;
	position: relative;
	margin: 1rem auto;
	animation: showAnswer 0.5s ease-in-out forwards;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 2rem;
	border: 2px solid orange;
	border-radius: 50px;
	width: 90%;
	cursor: pointer;
}

.answer:hover {
	background-color: orange;
}

.orange {
	background-color: orange;
}

.answer img {
	position: relative;
	width: 90%;
}

.answer .answer-text {
	display: flex;
	align-items: center;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	width: 80%;
	height: 90%;
	color: #fff;
}

.answer .answer-text p {
	color: #fff;
	font-size: 1rem;
	padding-left: 15px;
}

.answer-text span {
	color: orange;
}

.hide-answer {
	animation: hideAnswer 0.4s ease-in-out forwards;
}

@keyframes showAnswer {
	0% {
		opacity: 0;
		transform: scale(0);
	}
	75% {
		transform: scale(1.2);
	}
	100% {
		opacity: 1;
		transform: scale(1);
	}
}

@keyframes hideAnswer {
	0% {
		opacity: 1;
		transform: scale(1);
	}
	25% {
		transform: scale(1.2);
	}
	100% {
		opacity: 0;
		transform: scale(0);
	}
}

@media screen and (min-width: 768px) {
	.answer {
		width: 100%;
	}
}

@media screen and (max-width: 300px) {
	.answer .answer-text p {
		font-size: 0.75rem;
	}
}
