.answer-popup-outer {
	position: absolute;
	inset: 0;
	background-color: rgba(0, 0, 0, 0.9);
	opacity: 0;
	animation: answer-popup-fade-in 0.4s ease-in-out forwards;
}

.answer-popup-container {
	position: absolute;
	width: 90%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	text-align: center;
	border-radius: 10px;
	border: 2px solid var(--dark-blue);
	color: #fff;
	background-color: var(--dark-blue);
}

.answer-popup__padding {
	padding: 20px 0;
}

.answer-popup__message {
	font-size: 2rem;
}

.answer-popup__correct {
	background-color: #fff;
	color: #000;
	padding: 30px 5px;
	font-size: 1.25rem;
}

.answer-popup__button {
	background-color: var(--orange);
	border-radius: 0 0 10px 10px;
	font-size: 1.25rem;
	cursor: pointer;
	transition: 0.4s ease-in-out;
}

.answer-popup__button:hover {
	filter: brightness(80%);
}

@keyframes answer-popup-fade-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@media screen and (min-width: 768px) {
	.answer-popup-container {
		width: 30%;
		/* transform: translate(-90%, -45%); */
	}
}
