.timer {
	font-size: 3rem;
	color: white;
	border-radius: 50%;
	border: 0.3125rem solid #fff;
	width: 5rem;
	height: 5rem;
	display: flex;
	justify-content: center;
	align-items: center;
	opacity: 0;
	position: relative;
	margin-left: 20%;
}

.timer-text-anim {
	animation: bounceText 1s ease-in-out infinite;
}

.show-timer {
	animation: showTimer 1s ease-in-out forwards;
}

@keyframes bounceText {
	0% {
		opacity: 0;
		transform: scale(0);
	}
	20% {
		opacity: 1;
		transform: scale(1);
	}
	90% {
		opacity: 1;
	}
	100% {
		opacity: 0;
		transform: scale(1);
	}
}

@keyframes showTimer {
	0% {
		opacity: 0;
		transform: scale(0);
	}
	100% {
		opacity: 1;
		transform: scale(1);
	}
}

@media screen and (min-width: 768px) {
	.timer {
		width: 6rem;
		height: 6rem;
	}
}
